import React, { useEffect, useState } from "react";
import styled from "styled-components";
import QuestionContainer from "./QuestionContainer";
import { getPostTitle } from "../../../api/service-api/clubPostApi";
import { getUserProfile } from "../../../api/service-api/profileApi";
import { UserProfileResType } from "../../../interfaces/user-type";
import { Link, useNavigate } from "react-router-dom";

type ReviewContentContainerProps = {
  setSumScore: (sum: number) => void;
  type: "peer" | "club";
  postId: string;
  peerId?: string;
};

const peerReviewList = [
  "1.    ?",
  "2.    ?",
  "3.     ?",
  "4.     ?",
  "5.     ?",
];
const clubReviewList = [
  "1.    ?",
  "2.       ?",
  "3.      ?",
  "4.   ?",
  "5.    ?",
];

export default function ReviewContentContainer({
  setSumScore,
  type,
  postId,
  peerId,
}: ReviewContentContainerProps) {
  const navigate = useNavigate();
  const [scoresList, setScoresList] = useState(
    Array.from({ length: 5 }, () => 0)
  );
  const [postTitle, setPostTitle] = useState();
  const [peer, setPeer] = useState<UserProfileResType>();
  const handleScore = (score: number, num: number) => {
    const scoresArr = [...scoresList];
    scoresArr[num] = score;
    setScoresList(scoresArr);
  };

  const moveToPost = () => {
    navigate(`/clubDetail/${postId}`);
  };

  useEffect(() => {
    if (scoresList.includes(0)) return;
    const sum = scoresList.reduce((prev, cur) => prev + cur);
    setSumScore(Number(sum.toFixed(1)));
  }, [scoresList]);

  useEffect(() => {
    const getTitle = async () => {
      const res = await getPostTitle(postId);
      setPostTitle(res);
    };
    getTitle();
  }, [postTitle]);

  useEffect(() => {
    if (peerId) {
      const getUserData = async () => {
        const res = await getUserProfile(peerId);
        setPeer(res);
      };
      getUserData();
    }
  }, [peerId]);

  return (
    <Container>
      {type === "peer" ? (
        <Title>
          <ClubName>
            <Link to={`/clubDetail/${postId}`} target="_blank">
              {postTitle}
            </Link>
          </ClubName>{" "}
          
          <br /> {peer?.nickname}    !
        </Title>
      ) : (
        <Title>
          <ClubName>
            <Link to={`/clubDetail/${postId}`} target="_blank">
              {postTitle}
            </Link>
          </ClubName>{" "}
            ?
          <br />   !
        </Title>
      )}
      <ContentContainer>
        {type === "peer"
          ? peerReviewList.map((q, i) => (
              <QuestionContainer
                questionText={q}
                setScore={handleScore}
                key={`question_${i}`}
                qustionNum={i}
              />
            ))
          : clubReviewList.map((q, i) => (
              <QuestionContainer
                questionText={q}
                setScore={handleScore}
                key={`question_${i}`}
                qustionNum={i}
              />
            ))}
      </ContentContainer>
    </Container>
  );
}

const Container = styled.div`
  width: 100%;
  margin: 2.25rem 0 0;
`;
const Title = styled.h2`
  font-size: 1.25rem;
  font-weight: 500;
  line-height: 1.6rem;
`;
const ClubName = styled.span`
  cursor: pointer;
  color: var(--blue);
`;
const ContentContainer = styled.div`
  margin-top: 3.4rem;
  display: flex;
  flex-direction: column;
`;
