import { atom, selector, selectorFamily } from "recoil";
import { ProfileResType } from "../interfaces/user-type";
import { recoilPersist } from "recoil-persist";
import { getUserProfile } from "../api/service-api/profileApi";

// recoil - persist
const { persistAtom } = recoilPersist({
  key: "user",
  storage: sessionStorage,
});

//  
export const isLoggedInState = atom<Boolean>({
  key: "isLoggedInState",
  default: false,
  effects: [
    ({ setSelf, onSet }) => {
      const data = localStorage.getItem("accessToken");
      // setSelf: atom    
      if (data) setSelf(true);
      else setSelf(false);

      // onSet : atom    , Storage  
      // setSelf   
    },
  ],
  effects_UNSTABLE: [persistAtom],
});

// Access Token
export const accessTokenState = atom<string>({
  key: "accessTokenState",
  default: "",
  effects_UNSTABLE: [persistAtom],
});

//     Selector
export const getAccessTokenState = selector({
  key: "getAccessTokenState", //  
  get: ({ get }) => {
    return get(accessTokenState); // Atom    
  },
});

//    (   ) // fix
export const myUserIdState = atom<string>({
  key: "myUserIdState",
  default: "",
});

//  
export const myProfileInfoState = atom<ProfileResType>({
  key: "myProfileInfoState",
  //    setState  !!!! fix
  default: {
    user_id: "", 
    photo_profile: "",
    nickname: "",
    gender: "",
    birth_year: "",
    reliability_user: 0,
  },
  effects_UNSTABLE: [persistAtom],
});
